"use client"

import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Progress } from '@/components/ui/progress'

const quizSchema = z.object({
  oiliness: z.string().min(1, "Please select an option"),
  poreSize: z.string().min(1, "Please select an option"),
  sensitivity: z.string().min(1, "Please select an option"),
  breakouts: z.string().min(1, "Please select an option"),
  skinFeel: z.string().min(1, "Please select an option"),
  weatherReaction: z.string().min(1, "Please select an option"),
})

type QuizFormData = z.infer<typeof quizSchema>

const questions = [
  {
    id: 'oiliness',
    title: 'How does your skin feel by midday?',
    description: 'Think about how your skin typically feels after a few hours without touching it.',
    options: [
      { value: 'very-oily', label: 'Very oily and shiny all over', description: 'Noticeable shine on forehead, nose, cheeks, and chin' },
      { value: 'oily-tzone', label: 'Oily in T-zone only', description: 'Shine mainly on forehead, nose, and chin' },
      { value: 'balanced', label: 'Comfortable and balanced', description: 'No excessive oil or dryness' },
      { value: 'tight-dry', label: 'Tight and dry', description: 'Feels stretched or uncomfortable' },
    ]
  },
  {
    id: 'poreSize',
    title: 'How would you describe your pores?',
    description: 'Look closely at your skin in natural light.',
    options: [
      { value: 'large-visible', label: 'Large and easily visible', description: 'Especially noticeable on nose and cheeks' },
      { value: 'medium-tzone', label: 'Medium-sized in T-zone', description: 'More visible on forehead, nose, and chin' },
      { value: 'small-fine', label: 'Small and fine', description: 'Barely noticeable across the face' },
      { value: 'barely-visible', label: 'Barely visible', description: 'Very fine texture overall' },
    ]
  },
  {
    id: 'sensitivity',
    title: 'How does your skin react to new products?',
    description: 'Consider your typical experience when trying new skincare items.',
    options: [
      { value: 'very-reactive', label: 'Very reactive', description: 'Often gets red, itchy, or irritated' },
      { value: 'somewhat-sensitive', label: 'Somewhat sensitive', description: 'Occasional mild reactions' },
      { value: 'generally-tolerant', label: 'Generally tolerant', description: 'Rare reactions to most products' },
      { value: 'very-tolerant', label: 'Very tolerant', description: 'Almost never reacts negatively' },
    ]
  },
  {
    id: 'breakouts',
    title: 'How often do you experience breakouts?',
    description: 'Think about your typical skin condition over the past few months.',
    options: [
      { value: 'frequent', label: 'Frequently', description: 'Multiple breakouts most of the time' },
      { value: 'occasional', label: 'Occasionally', description: 'A few breakouts here and there' },
      { value: 'rarely', label: 'Rarely', description: 'Very infrequent breakouts' },
      { value: 'never', label: 'Almost never', description: 'Clear skin most of the time' },
    ]
  },
  {
    id: 'skinFeel',
    title: 'After cleansing, how does your skin feel?',
    description: 'Consider the immediate feeling after washing your face with a gentle cleanser.',
    options: [
      { value: 'tight-uncomfortable', label: 'Tight and uncomfortable', description: 'Feels stretched and needs immediate moisturizer' },
      { value: 'slightly-tight', label: 'Slightly tight', description: 'Mildly uncomfortable but manageable' },
      { value: 'comfortable', label: 'Comfortable', description: 'Feels clean and balanced' },
      { value: 'still-oily', label: 'Still feels oily', description: 'Not completely clean, some oil remains' },
    ]
  },
  {
    id: 'weatherReaction',
    title: 'How does weather affect your skin?',
    description: 'Think about seasonal changes and different climates.',
    options: [
      { value: 'dry-winter', label: 'Gets very dry in winter', description: 'Needs extra moisture in cold weather' },
      { value: 'oily-summer', label: 'Gets very oily in summer', description: 'Becomes shinier in hot, humid weather' },
      { value: 'varies-season', label: 'Changes with seasons', description: 'Different needs throughout the year' },
      { value: 'stays-same', label: 'Stays fairly consistent', description: 'Weather doesn\'t significantly affect it' },
    ]
  },
]

export default function SkinQuizForm() {
  const [currentStep, setCurrentStep] = useState(0)
  const [result, setResult] = useState<string | null>(null)

  const form = useForm<QuizFormData>({
    resolver: zodResolver(quizSchema),
  })

  const progress = ((currentStep + 1) / questions.length) * 100

  const onSubmit = (data: QuizFormData) => {
    // Simple skin type determination logic
    const answers = Object.values(data)
    
    const oilyCount = answers.filter(answer => 
      answer.includes('oily') || answer === 'large-visible' || answer === 'still-oily'
    ).length

    const dryCount = answers.filter(answer => 
      answer.includes('dry') || answer.includes('tight') || answer === 'barely-visible'
    ).length

    const sensitiveCount = answers.filter(answer => 
      answer.includes('reactive') || answer.includes('sensitive')
    ).length

    let skinType = 'Normal'
    let description = 'Your skin appears to be well-balanced with minimal concerns.'
    let recommendations = [
      'Use a gentle cleanser twice daily',
      'Apply a lightweight moisturizer',
      'Don\'t forget daily SPF protection',
      'Consider a weekly gentle exfoliant'
    ]

    if (oilyCount >= 3) {
      skinType = 'Oily'
      description = 'Your skin produces excess oil, especially in the T-zone area.'
      recommendations = [
        'Use a foaming or gel-based cleanser',
        'Look for oil-free, non-comedogenic moisturizers',
        'Consider salicylic acid for pore care',
        'Use clay masks 1-2 times per week'
      ]
    } else if (dryCount >= 3) {
      skinType = 'Dry'
      description = 'Your skin lacks moisture and may feel tight or uncomfortable.'
      recommendations = [
        'Use a creamy, hydrating cleanser',
        'Apply a rich moisturizer twice daily',
        'Look for ingredients like hyaluronic acid',
        'Avoid harsh scrubs and over-cleansing'
      ]
    } else if (sensitiveCount >= 2) {
      skinType = 'Sensitive'
      description = 'Your skin is reactive and needs gentle, fragrance-free products.'
      recommendations = [
        'Choose fragrance-free, hypoallergenic products',
        'Patch test new products before full use',
        'Look for soothing ingredients like aloe vera',
        'Avoid harsh actives and strong fragrances'
      ]
    } else if (oilyCount >= 1 && dryCount >= 1) {
      skinType = 'Combination'
      description = 'Your skin has both oily and dry areas, typically oily T-zone with drier cheeks.'
      recommendations = [
        'Use different products for different areas',
        'Gentle cleanser for the entire face',
        'Lightweight moisturizer on T-zone, richer on cheeks',
        'Consider targeted treatments for specific areas'
      ]
    }

    setResult(`${skinType}|${description}|${recommendations.join('|')}`)
  }

  const nextStep = () => {
    if (currentStep < questions.length - 1) {
      setCurrentStep(currentStep + 1)
    }
  }

  const prevStep = () => {
    if (currentStep > 0) {
      setCurrentStep(currentStep - 1)
    }
  }

  if (result) {
    const [skinType, description, ...recommendations] = result.split('|')
    
    return (
      <div className="space-y-6">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-neutral-900 mb-2">
            Your Skin Type: <span className="text-rose-500">{skinType}</span>
          </h2>
          <p className="text-neutral-600 mb-6">{description}</p>
        </div>

        <Card className="bg-gradient-to-r from-rose-50 to-blue-50">
          <CardHeader>
            <CardTitle className="text-lg font-semibold text-neutral-900">
              Personalized Recommendations
            </CardTitle>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2">
              {recommendations.map((rec, index) => (
                <li key={index} className="flex items-start">
                  <span className="text-rose-500 mr-2 mt-1">â€¢</span>
                  <span className="text-neutral-700">{rec}</span>
                </li>
              ))}
            </ul>
          </CardContent>
        </Card>

        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <Button 
            onClick={() => {
              setResult(null)
              setCurrentStep(0)
              form.reset()
            }}
            variant="outline"
            className="border-neutral-300 text-neutral-700 hover:bg-neutral-50"
          >
            Retake Quiz
          </Button>
          <Button 
            onClick={() => window.location.href = '/routine-builder'}
            className="bg-rose-500 hover:bg-rose-600 text-white"
          >
            Build My Routine
          </Button>
        </div>
      </div>
    )
  }

  const currentQuestion = questions[currentStep]

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        {/* Progress Bar */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm text-neutral-600">
            <span>Question {currentStep + 1} of {questions.length}</span>
            <span>{Math.round(progress)}% Complete</span>
          </div>
          <Progress value={progress} className="h-2" />
        </div>

        {/* Current Question */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg font-semibold text-neutral-900">
              {currentQuestion.title}
            </CardTitle>
            <CardDescription className="text-neutral-600">
              {currentQuestion.description}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <FormField
              control={form.control}
              name={currentQuestion.id as keyof QuizFormData}
              render={({ field }) => (
                <FormItem>
                  <FormControl>
                    <RadioGroup
                      onValueChange={field.onChange}
                      value={field.value}
                      className="space-y-3"
                    >
                      {currentQuestion.options.map((option) => (
                        <div key={option.value} className="flex items-start space-x-3 p-3 rounded-lg border border-neutral-200 hover:bg-neutral-50 transition-colors">
                          <RadioGroupItem value={option.value} id={option.value} className="mt-1" />
                          <div className="flex-1">
                            <FormLabel htmlFor={option.value} className="font-medium text-neutral-900 cursor-pointer">
                              {option.label}
                            </FormLabel>
                            <p className="text-sm text-neutral-600 mt-1">
                              {option.description}
                            </p>
                          </div>
                        </div>
                      ))}
                    </RadioGroup>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </CardContent>
        </Card>

        {/* Navigation Buttons */}
        <div className="flex justify-between">
          <Button
            type="button"
            variant="outline"
            onClick={prevStep}
            disabled={currentStep === 0}
            className="border-neutral-300 text-neutral-700 hover:bg-neutral-50"
          >
            Previous
          </Button>
          
          {currentStep === questions.length - 1 ? (
            <Button 
              type="submit"
              className="bg-rose-500 hover:bg-rose-600 text-white"
              disabled={!form.watch(currentQuestion.id as keyof QuizFormData)}
            >
              Get My Results
            </Button>
          ) : (
            <Button
              type="button"
              onClick={nextStep}
              disabled={!form.watch(currentQuestion.id as keyof QuizFormData)}
              className="bg-rose-500 hover:bg-rose-600 text-white"
            >
              Next
            </Button>
          )}
        </div>
      </form>
    </Form>
  )
}
